        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin: 0rem;
        padding: 1rem 0rem;
        font-weight: 300;
        line-height: 1.2;
    }
    .password {
        display: flex;
        flex: 0 1 auto;
        justify-content: flex-start;
        align-content: flex-start;
        text-align: left;
        font-size: 1rem;
        background: -webkit-linear-gradient(#2D2D2D, #FFD464);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
