@media (max-width:382px) { 
    .buttonContainer{
        justify-content: space-around;
        height: 7rem;
        flex-flow: column;
    }
}

/* Search Form Styles */

.header {
    display: flex;
    flex-flow: row wrap;
    align-content: center;
}
.container {
    max-width: 33%;
    display: flex;
    flex: 1 1 auto;
    flex-flow: column wrap;
    justify-content: center;
    align-content: center;
    order: 2;
}
.display-4 {
    display: flex;
    flex: 0 1 auto;
    justify-content: center;
    align-content: flex-end;
