}
  .card {
    display: flex;
    flex-flow: column wrap;
    background: rgb(0, 0, 0);
    border-radius: .5rem;
    border-color: #f4f4f4;
    justify-content: flex-end;
    align-content: flex-start;
    position: relative;
    overflow: hidden;
    width: 244px;
    height: 308px;
  }
  
  /* Slide */
  img {
    position: absolute; 
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 0, 0)), color-stop(rgba(0, 0, 0, 0.397)), to(rgba(0, 0, 0, 0))); 
    -webkit-mask-image: -webkit-linear-gradient(top, rgb(0, 0, 0), rgba(0, 0, 0, 0.397), rgba(0, 0, 0, 0)); 
            mask-image: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 0, 0)), color-stop(rgba(0, 0, 0, 0.397)), to(rgba(0, 0, 0, 0))); 
            mask-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0, 0, 0, 0.397), rgba(0, 0, 0, 0));
    width: 100%;
    height: 100%;
  }
  .info-container {
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  ul {
    list-style-type: none;
    padding: 0px;
  }
