    color: white;
    font-weight: 200;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.253);
  }
  .card-info {
    padding: 0rem .6rem;
  }
  .art_title {
    font-weight: 500;
  }
  .medium {
    font-size: .8rem;
  }
  .art_date {
    display: flex;
    flex: 1 1 auto;
    flex-flow: column nowrap;
    font-size: .8rem;
  }
  .nationality {
    display: flex;
    flex: 1 1 auto;
    flex-flow: column nowrap;
    font-size: .8rem;
  }
.nav-link-container{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    order: 3;
}
.nav-item-link {
    display: flex;
